    margin:0;
    padding:0;
    vertical-align:top;
    text-align:center
}
.jstree-icon:empty{
    display:inline-block;
    text-decoration:none;
    margin:0;
    padding:0;
    vertical-align:top;
    text-align:center
}
.jstree-ocl{
    cursor:pointer
}
.jstree-leaf>.jstree-ocl{
